body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
.avatar {
  padding-top: 10px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.avatar .point-wrapper {
  position: absolute;
}
.avatar .point {
  border-radius: 100%;
}
.avatar .right-side {
  width: 70%;
  height: auto;
  position: absolute;
  right: 0;
  top: 10px;
  bottom: 0;
  left: 0;
  margin: auto;
}
.avatar .right-side * {
  pointer-events: none;
}
.avatar .avatar-img {
  width: 70%;
  -webkit-align-self: center;
          align-self: center;
  opacity: 0;
  -webkit-animation: fadeIn .8s .5s ease-in forwards;
          animation: fadeIn .8s .5s ease-in forwards;
  pointer-events: none;
}

@-webkit-keyframes fadeIn {
  0% {
  opacity: 0;
  }
  100% {
  opacity: .8;
  }
}

@keyframes fadeIn {
  0% {
  opacity: 0;
  }
  100% {
  opacity: .8;
  }
}
header {
  width: 300px;
  background: #252526;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
header .name {
  padding: 30px;
  color: #fff;
  font-size: 38px;
  text-align: center;
}

header .sign {
  color: #ccc;
  font-size: 16px;
  letter-spacing: .12em;
  text-align: center;
  font-family: Helvetica;
  padding: 10px;
}

header .sns {
  list-style: none;
  margin: 0;
  padding: 0;
  color: #ccc;
  text-align: center;
}

header .sns li {
  font-size: 14px;
  line-height: 2em;
  margin: 0;
  letter-spacing: 2px;
}

header .sns li a {
  color: #ccc;
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: baseline;
          align-items: baseline;
}
header .sns li a:hover {
  font-size: 18px;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
header .sns li a:hover.blog {
  color: #009B00;
}
header .sns li a:hover.github {
  color: #fff;
}
header .sns li a:hover.weibo {
  color: red;
}
header .sns li a:hover.zhihu {
  color: rgb(15, 136, 235);
}
header .sns li a::before {
  content: "[";
}
header .sns li a::after {
  content: "]";
}
header .sns li a::before, header .sns li a::after {
  font-size: 20px;
  display: block;
  width: 0;
  opacity: 0;
  -webkit-transition: opacity .3s ease-in-out,
        width .3s ease-in-out;
  transition: opacity .3s ease-in-out,
        width .3s ease-in-out;
}
header .sns li a:hover::before, header .sns li a:hover::after {
  opacity: 1;
  width: 25px;
}
header .bottom {
  color: #ddd;
  padding: 10px;
  text-align: center;
  font-family: Helvetica;
  font-size: 12px;
}

.app {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
}

